.main__container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.main__title {
    color: #374A3D;
font-size: 72px;
font-style: normal;
font-weight: 600;
line-height: 76px;
font-variant: small-caps;

padding: 120px 0 20px 0;
}
.main__title span {
    color: #0CA941;
}
.main__item {
    color: #707070;
font-family: Gilroy;
font-size: 18px;
font-style: normal;
font-weight: 500;
line-height: 32px;
font-variant: small-caps;
}
.main_btn button {
    margin-top: 30px;
    color: #000;
font-family: Gilroy;
font-size: 18px;
font-style: normal;
font-weight: 600;
line-height: 18px;
font-variant: small-caps;
background-color: #89f3ac;
padding: 10px 20px;
border-radius: 20px;

}

.main__img img {
    width: auto;
    height: 600px;
}
.main__block {
   position: relative;
    margin: 40px 0;
    display: flex;
    justify-content: space-around;
}
.main_block-title {
    color: #374A3D;
font-family: Fredoka One;
font-size: 30px;
font-style: normal;
font-weight: 600;
line-height: 40px;
font-variant: small-caps;
}
.main_block-text {
    color: #5B7262;
font-family: Gilroy;
font-size: 16px;
font-style: normal;
font-weight: 500;
line-height: 26px;
font-variant: small-caps;
}

.main_prices {
    display: flex;
}
.price__item {
    padding: 0 20px;
   text-align: center;
   
}
.price {
    color: #0CA941;
font-family: Gilroy;
font-size: 60px;
font-weight: 800;
line-height: 42px;
font-variant: small-caps;
padding-bottom: 20px;

}
.price-text {

font-size: 16px;

font-weight: 500;
color: #045720;

font-variant: small-caps;
}
@media screen and (max-width:1200px) {
    .main__img img {
        width: auto;
        height: 500px;
    }
    .main__title {
    font-size: 65px;
    line-height: 55px;
    }
    .main__item {
    font-size: 18px;
    line-height: 32px;

    }
    .main_prices {
        text-align: center;
        justify-content: unset;
    }
    .main__block {
         text-align: center;
     }
    

}
@media screen and (max-width:992px) {
    .price {
        font-size: 40px;
        font-weight: 700;
        line-height: 38px;
    }
    .main__block {
        position: relative;
         display: flex;
         flex-direction: column;
         justify-content: unset;
         text-align: center;
     }

    .main__img img {
        width: auto;
        height: 300px;
    }
    .main__title {
        font-size: 50px;
        line-height: 45px;
        }
        .main__item {
        font-size: 16px;
        line-height: 26px;
    
        }
    
}
@media screen and (max-width:768px) {
    .price {
        font-size: 35px;
        font-weight: 600;
        line-height: 34px;
    }
    

    .main__container {
        display: flex;
        flex-direction: column;
        justify-content: unset;
        align-items: center;
        

    }

    .main_btn {
        text-align: center;
    }
    .main__img img {
        width: auto;
        height: 350px;
    }
    .main__title {
        font-size: 35px;
        line-height: 30px;
        text-align: center;
        }
        .main__item {
        font-size: 14px;
        line-height: 20px;
        text-align: center;
    
        }
    
}
@media screen and (max-width:576px) {
    .main__img img {
        width: auto;
        height: 250px;
    }
    .price {
    font-size: 30px;
    font-weight: 600;
    line-height: 30px;
}

    
}