.footer__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    text-align: center;
    margin: 50px 0;
}
.footer__item {

}
.footer__title {
    color: #374A3D;
font-family: Gilroy;
font-size: 22px;
font-style: normal;
font-weight: 700;
line-height: 32px; /* 145.455% */
font-variant: small-caps;
}
.footerLinks a {
    color: #678570;
font-family: Gilroy;
font-size: 18px;
font-style: normal;
font-weight: 500;
line-height: 50px;
font-variant: small-caps;
}
.copy-right {
    padding: 20px 0;
    background-color: #000;
    color: #fff;
    font-weight: 600;
    font-size: 20px;
    text-align: center;
}
@media screen and (max-width: 768px) {
    .footerLinks a {
    line-height: 30px;

    }
    .footer__item {
        margin-top: 20px;
    }
    .footer__container {
        grid-template-columns: repeat(2, 1fr);
    }
}
@media screen and (max-width: 576px) {
    .footerLinks a {
    line-height: 24px;
    }
    .footer__container {
        grid-template-columns: repeat(1, 1fr);
    }
}