.about_container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.About__item2 {
    margin-top: 70px;
    color: #374A3D;
font-family: Fredoka One;
font-size: 36px;
font-style: normal;
font-weight: 400;
line-height: 44px;
font-variant: small-caps;
}

.About__item3 {
    color: #678570;
font-family: Gilroy;
font-size: 18px;
font-style: normal;
font-weight: 500;
line-height: 32px;
font-variant: small-caps;
}
.img img {
    width: auto;
    height: 550px;
}
@media screen and (max-width:1200px) {
    .img img {

        height: 430px;
    }
    .About__item2 {
        margin-top: 30px;
    font-size: 30px;
    line-height: 38px;
    }
    
    .About__item3 {
    font-size: 16px;
    line-height: 28px;

    }
}
.service__block {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.service__title {
    color: #374A3D;
font-family: Fredoka One;
font-size: 36px;
font-style: normal;
font-weight: 400;
line-height: 60px; /* 166.667% */
font-variant: small-caps;
}
.service__text {
    color: #678570;
font-family: Gilroy;
font-size: 18px;
font-style: normal;
font-weight: 500;
line-height: 32px; /* 177.778% */
font-variant: small-caps;
margin: 20px 0;
}
.service__img img {
    width: auto;
    height: 500;
}
.dream__block {
    margin: 30px 0;
}
.dream__title {
    color: #374A3D;
text-align: center;
font-size: 36px;
font-style: normal;
font-weight: 400;
line-height: 60px; /* 166.667% */
font-variant: small-caps
}
.dream__text {
    margin: 20px 0;
    color: #678570;
text-align: center;
font-size: 18px;
font-style: normal;
font-weight: 500;
line-height: 24px; /* 177.778% */
align-items: center;
font-variant: small-caps;
}
.dream__flower {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    text-align: center;
}
.flower__container img {
    height: 240px;
}
.idea__block {
    margin: 30px auto;
    text-align: center;
}
.idea__title {
    color: #374A3D;
font-family: Fredoka One;
font-size: 36px;
font-style: normal;
font-weight: 600;
line-height: 60px; /* 166.667% */
font-variant: small-caps;
}
.idea__text {
    color: #678570;
font-family: Gilroy;
font-size: 18px;
font-style: normal;
font-weight: 500;
line-height: 32px; /* 177.778% */
font-variant: small-caps;
align-items: center;
}


.idead__form {
    display: flex;
    justify-content: center;
}
.idead__form {
    margin: 30px 0;
}
.idead__form input {
    border-radius: 10px 0 0  10px;
border: 1px solid #0CA941;

    outline: none;
    padding: 14px 8px;
}
.idead__form button {
    border-radius: 0 10px  10px  0;
    padding: 12px 8px;
background: #0CA941;
color: #fff;
padding: 12px 8px;
font-size: 16px;
border: 1px solid #0CA941;
}
.idead__form button:hover {
    background: #09a43c;
    color: #dcdcdc;
}
@media screen and (max-width:1200px) {
    .service__img img {
        height: 450;
    }
    .flower__container img {
        height: 210px;
    }
    
}
@media screen and (max-width: 992px) {
    .dream__flower {
        grid-template-columns: repeat(2, 1fr);
    }
    .flower__container img {
        height: 190px;
    }
    .service__block {
        display: flex;
        flex-direction: column;
        align-items: unset;
        text-align: center;
    }
    .service__img img {
        height: 400;
    }
    .img img {

        height: 390px;
    }
    .about_container {
        flex-direction: column-reverse;
        justify-content: unset;
        text-align: center;
    }
}
@media screen and (max-width: 768px) {
    .idea__title {
    font-size: 32px;
    line-height: 40px; /* 166.667% */
    }
    .idea__text {
    font-size: 16px;
    line-height: 25px; /* 177.778% */
    }
    .dream__flower {
        grid-template-columns: repeat(1, 1fr);
    }
    .flower__container img {
        height: 170px;
    }
    .service__img img {
        height: 330;
    }
}
@media screen and (max-width: 576px) {
    .idea__title {
        font-size: 26px;
        line-height: 32px; /* 166.667% */
        }
        .idea__text {
        font-size: 12px;
        line-height: 20px; /* 177.778% */
        }
    .flower__container img {
        height: 140px;
    }
    

    .service__img img {
        height: 280px;
    }
    .service__title {
    font-size: 28px;
    line-height: 40px; /* 166.667% */

    }
    .service__text {
    font-size: 16px;
    line-height: 24px; /* 177.778% */

    }
    .About__item2 {
        margin-top: 15px;
        font-size: 20px;
        line-height: 22px;
        }
        
        .About__item3 {
        font-size: 12px;
        line-height: 16px;
    
        }
    .img img {

        height: 250px;
    }
}