.header {
  width: 100%;
}

.box {
  height: 80px;
  display: flex;
  justify-content: space-between;

}
a {
  text-decoration: none;
}

.logo_image {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-left: 70px;
}

.menu {
  display: flex;
  align-items: center;
  gap: 30px;
  margin-right: 13%;
}
.menu a {

  font-family: Gilroy;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
transition: all ease 0.3s;
}


.mobile_btn {
  display: none;
}
.Login {
  color: #fff;
  border-radius: 6px;
background: #0CA941;
padding: 4px 16px;
}
.Login:hover {
  color: rgb(178, 178, 178);
}
@media screen and (max-width: 960px) {
  .menu {
    margin-right: 4%;
  }
}

@media (max-width: 800px) {
  .mobile_btn {
    display: block;
    position: absolute;
    right: 10px;
    top: 27px;
    cursor: pointer;
    z-index: 10;
  }

  .menu {
    margin-right: unset;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    right: -10%;
    opacity: 0;
    
    top: 0;
  
    height: 350px;
    width: 240px;
    background-color: rgb(183, 255, 145);
    z-index: 10;
    transition: all ease 0.2s;
    border-radius: 0 0 0 40px;

  }

  .active {
    right: 0;
    opacity: 1;
  }

  .menu a {
    font-size: 26px;
    


  }
  .logo_image {
    margin-left: 5%;
  }
}
